import React, { useState, useEffect } from 'react';
import { object, array, func } from 'prop-types';
import { produce } from 'immer';

import GroupAddIcon from '@material-ui/icons/GroupAdd';
import GroupIcon from '@material-ui/icons/Group';
import Avatar from '@material-ui/core/Avatar';
import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiAccountGroup } from '@mdi/js';

import { CONSTANTS } from 'config';
import { RestClient } from 'shared/lib/rest';

import { Button } from 'shared/components/controls';

import { Paper } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';
import { Modal, ModalHeader, ModalFooter, Result } from 'shared/components/overlays';

import { AvatarCard } from '../../../../components/AvatarCard/AvatarCard';

import { SearchableList } from '../../../../components/SearchableList/SearchableList';

import './GroupsSelection.local.scss';

export const GroupsSelection = ({ selected, helpers }) => {
  const [open, setOpen] = useState(false);

  const openModal = () => {
    setOpen(true);
  };

  const closeModal = () => {
    setOpen(false);
  };

  const modalActions = {
    open: openModal,
    close: closeModal
  };

  /* eslint-disable jsx-a11y/click-events-have-key-events */
  return (
    <>
      <div tabIndex={0} role="button" className="add-to-group-button" onClick={openModal}>
        <AvatarCard
          label="Add to Group"
          avatar={{
            fallback: (
              <SvgIcon>
                <path d={mdiAccountGroup} />
              </SvgIcon>
            )
          }}
        />
      </div>
      <Modal open={open} actions={modalActions}>
        {({ aria, actions }) => (
          <Paper style={{ minWidth: '75vh', minHeight: '75vh' }}>
            <ModalHeader
              avatar={
                <Avatar className="generic-avatar" alt="edit-user-groups">
                  <GroupAddIcon />
                </Avatar>
              }
              title="Add to Group"
              desc="Please select the groups you want to add this user to"
              aria={aria}
              onClose={actions.close}
            />
            <GroupsList selected={selected} helpers={helpers} closeModal={actions.close} />
          </Paper>
        )}
      </Modal>
    </>
  );
};

GroupsSelection.propTypes = {
  selected: array,
  helpers: object
};

const GroupsList = ({ selected, helpers, closeModal }) => {
  const [state, setState] = useState({
    all: [],
    loading: true,
    result: undefined,
    done: false
  });

  useEffect(() => {
    RestClient.get(CONSTANTS.BASE.ENDPOINTS.GROUPS)
      .then(response => {
        const data = response.data.data.groups;

        setState(prev =>
          produce(prev, draft => {
            draft.all = data;
            draft.result = response.result;
            draft.done = true;
            draft.loading = false;
          })
        );
      })
      .catch(error => {
        setState(prev =>
          produce(prev, draft => {
            draft.all = [];
            draft.result = error.result;
            draft.done = true;
            draft.loading = false;
          })
        );
      });
  }, []);

  if (state.loading) {
    return <Loader />;
  }

  if (state.done && !state.all.length) {
    return (
      <>
        <Paper.Content>
          <Result type="fail">
            <Result.Text>Could not load groups list</Result.Text>
          </Result>
        </Paper.Content>
        <ModalFooter>
          <Button variant="contained" color="primary" onClick={closeModal}>
            Close
          </Button>
        </ModalFooter>
      </>
    );
  }

  return (
    <>
      <Paper.Content>
        <div className="seachable-list-container">
          <SearchableList
            items={state.all}
            filter={(item, query) => item.name.toLowerCase().includes(query.toLowerCase())}
            variant="row white"
          >
            {entry => {
              return (
                <div className="card-wrap" style={{ minWidth: '178px', maxWidth: '178px' }}>
                  <AvatarCard
                    onClick={() => {
                      const existing = selected.indexOf(entry.name);

                      if (existing !== -1) {
                        helpers.remove(existing);
                      } else {
                        helpers.push(entry.name);
                      }
                    }}
                    selected={selected.includes(entry.name)}
                    label={entry.name}
                    avatar={{ fallback: <GroupIcon /> }}
                  />
                </div>
              );
            }}
          </SearchableList>
        </div>
      </Paper.Content>
      <ModalFooter>
        <Button variant="contained" color="primary" onClick={closeModal}>
          Confirm
        </Button>
      </ModalFooter>
    </>
  );
};

GroupsList.propTypes = {
  selected: array,
  helpers: object,
  closeModal: func
};
