import React from 'react';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';

export default function MUIselect({ label, handleSelect, selectedOption, options, disabled }) {
  const [labelWidth, setLabelWidth] = React.useState(0);
  const inputLabel = React.useRef(null);
  const selectedValue = selectedOption ? selectedOption.value : '';

  React.useEffect(() => {
    setLabelWidth(inputLabel.current.offsetWidth);
  }, []);

  const handleChange = event => {
    const findItem = options.find(item => item.value === event.target.value);
    if (findItem) {
      handleSelect(findItem);
    }
  };

  return (
    <FormControl disabled={disabled} fullWidth variant="outlined">
      <InputLabel ref={inputLabel} htmlFor="demo-simple-select-outlined-label">
        {label}
      </InputLabel>
      <Select
        labelId="demo-simple-select-outlined-label"
        value={selectedValue}
        onChange={handleChange}
        labelWidth={labelWidth}
      >
        {options.map(item => (
          <MenuItem key={item.value} disabled={item.disabled} value={item.value}>
            {item.label}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
}
