import React from 'react';

import { any, func } from 'prop-types';

import { Overflow, Center } from 'shared/components/layout';

import './job-list.local.scss';

export const JobList = ({ items, children }) => {
  const noneAvailable = items.size === 0;

  if (noneAvailable) {
    return <NoJobs />;
  }

  return (
    <Overflow variant="white" component="ul">
      {items.map((item, index, list) => (
        <li key={index} className="list-item">
          {children(item, index, list)}
        </li>
      ))}
    </Overflow>
  );
};

JobList.propTypes = {
  items: any,
  children: func
};

JobList.defaultProps = {
  items: []
};

function NoJobs() {
  return (
    <Center>
      <p>There are no jobs available with these filters.</p>
    </Center>
  );
}
