import React from 'react';
import { connect } from 'react-redux';
import { func } from 'prop-types';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import {
  mdiChartTimelineVariant,
  mdiDatabase,
  mdiCheckboxMultipleBlankOutline,
  mdiShape,
  mdiLicense,
  mdiHexagonSlice2
} from '@mdi/js';

import { Paper } from 'shared/components/layout';
import { changeStateThunk } from '../../store/thunks';
import { ActionCard } from '../../components/ActionCard/ActionCard';
import { CardsLayout } from '../../components/CardsLayout/CardsLayout';

import { DatabaseSettings } from '../Settings/DatabaseSettings';

import { retrieveServerEntry, getBackup, getDBStatus } from '../../store/selectors';

export class AllDatabases extends React.PureComponent {
  render() {
    const { databases, retrieveDbBackup, retrieveDbStatus } = this.props;

    return (
      <>
        <Paper.Content>
          <CardsLayout>
            {databases.map(database => (
              <ActionCard
                key={database.name}
                lastBackup={retrieveDbBackup(database)}
                settings={<DatabaseSettings database={database} />}
                status={retrieveDbStatus(database)}
                type="database"
                icon={
                  <SvgIcon>
                    <Icon path={mdiDatabase} />
                  </SvgIcon>
                }
                label={database.special.database}
                onClick={() => this.setPath(database.path)}
                options={[
                  {
                    icon: (
                      <SvgIcon>
                        <Icon path={mdiChartTimelineVariant} />
                      </SvgIcon>
                    ),
                    label: 'Workload',
                    onClick: () =>
                      this.props.changeState({ path: database.path, level: 'workload' })
                  },
                  {
                    icon: (
                      <SvgIcon>
                        <Icon path={mdiCheckboxMultipleBlankOutline} />
                      </SvgIcon>
                    ),
                    label: 'Sessions',
                    onClick: () =>
                      this.props.changeState({ path: database.path, level: 'sessions' })
                  },
                  {
                    icon: (
                      <SvgIcon>
                        <Icon path={mdiShape} />
                      </SvgIcon>
                    ),
                    label: 'Objects',
                    onClick: () => this.props.changeState({ path: database.path, level: 'objects' })
                  },
                  {
                    icon: (
                      <SvgIcon>
                        <Icon path={mdiHexagonSlice2} />
                      </SvgIcon>
                    ),
                    label: 'Storage',
                    onClick: () => this.props.changeState({ path: database.path, level: 'storage' })
                  },
                  {
                    icon: (
                      <SvgIcon>
                        <Icon path={mdiLicense} />
                      </SvgIcon>
                    ),
                    label: 'License reporting',
                    onClick: () => this.props.changeState({ path: database.path, level: 'license' })
                  }
                ]}
              />
            ))}
          </CardsLayout>
        </Paper.Content>
      </>
    );
  }

  setPath = path => {
    this.props.changeState({ path, level: 'workload' });
  };
}

AllDatabases.propTypes = {
  changeState: func
};

export const ConnectedAllDatabases = connect(
  state => {
    const {
      structured,
      state: { path }
    } = state.explorer;

    const [protocol, address] = path.split('--');
    const server = retrieveServerEntry({ protocol, address }, structured);
    const backups = state.explorer.dbLastBackup;
    const statuses = state.explorer.dbStatus;

    return {
      databases: (server && server.databases) || [],
      retrieveDbBackup: database => getBackup(database, backups),
      retrieveDbStatus: database => getDBStatus(database, statuses)
    };
  },
  dispatch => ({
    changeState: newState => dispatch(changeStateThunk(newState))
  })
)(AllDatabases);
