import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';

import { connect } from 'react-redux';

import { getWorkloadData } from 'shared/lib/workload';

import { actions } from 'shared/store/actions';
import { jobsSelector, globalFiltersSelector } from 'shared/store/state';
import { Button } from 'shared/components/controls';
import { Loader } from 'shared/components/loaders';

import { Header } from 'core/components/Header';
import {
  WorkloadGraph,
  Center,
  RangeDatePicker,
  SimpleSearchInput,
  Switch
} from 'shared/components';
import { Main, Paper } from 'shared/components/layout';
import { NavigationTab } from 'shared/components/layout/NavigationTab/NavigationTab';
import { Result } from 'shared/components/overlays/Result/Result';

import { JobItem } from './components/job-item';
import { JobList } from './components/job-list';

import { Filters } from './containers/filters';
import { JobsInvocations } from './containers/jobs-invocations';

import { Schedule } from './containers/Schedule/Schedule';

import './jobs.local.scss';

function renderJobItem(item, index) {
  return <JobItem extended key={index} job={item} />;
}

class RootModule extends React.PureComponent {
  constructor(props) {
    super(props);

    const query = new URLSearchParams(this.props.location.search);

    this.state = {
      range: {
        startDate: moment.unix(props.globalFilters.get('startDate')),
        endDate: moment.unix(props.globalFilters.get('endDate'))
      },
      shell_host_address_filter: query.get('shell_host_address_filter'),
      protocol_filter: query.get('protocol_filter'),
      status: query.get('status'),
      database_name_filter: query.get('database_name_filter'),
      name_filter: '',
      selectedTab: 'all'
    };
  }

  render() {
    const {
      shell_host_address_filter,
      protocol_filter,
      status,
      database_name_filter,
      selectedTab
    } = this.state;

    const callFailed = this.props.jobs.get('callFailed');
    const reason = this.props.jobs.get('reason');

    if (callFailed && reason === 'rights' && !this.props.jobs.get('fetching')) {
      return (
        <>
          <Header title="Jobs" />
          <Main>
            <Paper>
              <Paper.Content>
                <Result type="fail">
                  <BodyText>
                    You don&apos;t have sufficient privileges to access invocation data. Please
                    contact your administrator.
                  </BodyText>
                </Result>
              </Paper.Content>
            </Paper>
          </Main>
        </>
      );
    }

    return (
      <JobsInvocations>
        <Header title="Jobs">
          <RangeDatePicker
            onDatesChange={this.onChangeDatesHandler}
            startDate={this.state.range.startDate}
            endDate={this.state.range.endDate}
          />
        </Header>
        <Main>
          <Paper>
            <Paper.Header>
              <NavigationTab
                tabs={[
                  { label: 'All jobs', value: 'all' },
                  { label: 'Upcoming', value: 'upcoming' }
                ]}
                active={selectedTab}
                onClick={this.handleOnTabChange}
              />
            </Paper.Header>
            <Switch value={selectedTab}>
              <Switch.Case value="all">
                <Paper.Header>
                  <div style={{ height: 220, width: '100%' }}>
                    {this.props.jobs.get('fetching') ? (
                      <Center>
                        <Loader>Computing workload</Loader>
                      </Center>
                    ) : (
                      <WorkloadGraph
                        data={getWorkloadData(this.props.jobs.get('content').toJS(), {
                          currentDate: moment.unix(
                            this.props.globalFilters.get('startDate'),
                            'YYYY-MM-DD'
                          ),
                          endDate: moment.unix(
                            this.props.globalFilters.get('endDate'),
                            'YYYY-MM-DD'
                          )
                        })}
                      />
                    )}
                  </div>
                </Paper.Header>
              </Switch.Case>
              <Switch.Case value="upcoming">{null}</Switch.Case>
            </Switch>
            <Paper.Content scrollable={false}>
              <div className="jobs-filters-controllers">
                <div className="job-search">
                  <SimpleSearchInput
                    value={this.state.name_filter}
                    onBlur={() => this.performFilter(this.state)}
                    onChange={this.onSearchInputChange}
                  />
                </div>
                <Filters
                  selections={{
                    shell_host_address_filter,
                    protocol_filter,
                    status,
                    database_name_filter
                  }}
                  onChange={this.handleOnFilterChange}
                  excludeStatus={selectedTab === 'upcoming'}
                />
              </div>
              {this.props.jobs.get('fetching') ? (
                <Center>
                  <Loader>Loading jobs list</Loader>
                </Center>
              ) : (
                <JobList items={this.returnListBasedOnTab()}>{renderJobItem}</JobList>
              )}
            </Paper.Content>
            <Paper.Footer>
              <Schedule />
              <Button to="/jobs/job">New Job</Button>
            </Paper.Footer>
          </Paper>
        </Main>
      </JobsInvocations>
    );
  }

  updateQSP = (filter, value) => {
    const query = new URLSearchParams(this.props.location.search);

    if (!value) {
      query.delete(filter);
    } else if (query.has(filter) && query.get(filter) !== value) {
      query.set(filter, value);
    } else {
      query.append(filter, value);
    }

    query.toString();

    this.props.history.push({ search: `?${query}` });
  };

  handleOnTabChange = tab => {
    this.setState({ selectedTab: tab });
  };

  handleOnFilterChange = (filter, value) => {
    this.updateQSP(filter, value);

    this.setState(
      {
        [filter]: value
      },
      () => {
        this.performFilter(this.state);
      }
    );
  };

  returnListBasedOnTab = () => {
    const { selectedTab, status } = this.state;
    const list = this.props.jobs.get('content');

    if (status) {
      return list.filter(item => item.get('status') === status);
    }

    if (selectedTab === 'upcoming') {
      return list.filter(item => item.get('status') === 'SCHEDULED');
    }

    return list;
  };

  performFilter = ({
    shell_host_address_filter,
    protocol_filter,
    status,
    database_name_filter,
    name_filter
  }) => {
    const { globalFilters } = this.props;

    const days = moment
      .unix(globalFilters.get('endDate'), 'YYYY-MM-DD')
      .diff(moment.unix(globalFilters.get('startDate'), 'YYYY-MM-DD'), 'days');

    const payload = {
      date_invoke: moment.unix(globalFilters.get('startDate')).format('YYYY-MM-DD'),
      period: days + 1,
      page_size: 2147483645
    };

    if (shell_host_address_filter) {
      payload.shell_host_address_filter = shell_host_address_filter;
    }

    if (protocol_filter) {
      payload.protocol_filter = protocol_filter;
    }

    if (status) {
      payload.status = status;
    }

    if (database_name_filter) {
      payload.database_name_filter = database_name_filter;
    }

    if (database_name_filter) {
      payload.database_name_filter = database_name_filter;
    }

    if (name_filter) {
      payload.name_filter = name_filter;
    }

    this.props.getJobs(payload);
  };

  onSearchInputChange = event => {
    this.setState({
      name_filter: event.target.value
    });
  };

  onChangeDatesHandler = range => {
    this.setState(
      {
        range: {
          startDate: range.startDate,
          endDate: range.endDate
        }
      },
      () => {
        this.props.setGlobalFilters(range);
        this.performFilter(this.state);
      }
    );
  };
}
RootModule.propTypes = {
  globalFilters: PropTypes.object.isRequired,
  setGlobalFilters: PropTypes.func.isRequired,
  jobs: PropTypes.object.isRequired,
  getJobs: PropTypes.func.isRequired
};

export const mapStateToProps = state => ({
  globalFilters: globalFiltersSelector(state),
  jobs: jobsSelector(state)
});

export const mapDispatchToProps = dispatch => ({
  getJobs: data => {
    dispatch(actions.getJobs.dispatchLoading(data));
  },
  setGlobalFilters: data => dispatch(actions.setGlobalFilters.dispatchLoading(data))
});

export default connect(mapStateToProps, mapDispatchToProps)(RootModule);

function BodyText({ children }) {
  return (
    <h2
      style={{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: '14px',
        lineHeight: '20px',
        letterSpacing: '0.25px',
        marginTop: '24px'
      }}
    >
      {children}
    </h2>
  );
}
