import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { getJobsFiltersOptions } from 'shared/store/selectors';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiHexagonMultipleOutline, mdiDatabase, mdiServer, mdiCircleSlice5 } from '@mdi/js';

import MUIselect from 'shared/components/MUIselect';
import './filters.scss';

const Filters = props => {
  const { filters, selections, onChange, excludeStatus } = props;
  const { shell_host_address_filter, protocol_filter, status, database_name_filter } = selections;

  return (
    <div className="job-filters">
      <MUIselect
        options={[
          { value: '', label: 'All' },
          ...filters.protocol.map(el => ({ value: el, label: el }))
        ]}
        selectedOption={{ value: protocol_filter }}
        handleSelect={({ value }) => onChange('protocol_filter', value)}
        label={
          <span className="job-filter-select-label-wrap">
            <SvgIcon>
              <path d={mdiHexagonMultipleOutline} />
            </SvgIcon>
            <span className="job-filter-select-label">DBMS</span>
          </span>
        }
      />
      <MUIselect
        options={[
          { value: '', label: 'All' },
          ...filters.database.map(el => ({ value: el, label: el }))
        ]}
        selectedOption={{ value: database_name_filter }}
        handleSelect={({ value }) => onChange('database_name_filter', value)}
        label={
          <span className="job-filter-select-label-wrap">
            <SvgIcon>
              <path d={mdiServer} />
            </SvgIcon>
            <span className="job-filter-select-label">Database</span>
          </span>
        }
      />
      <MUIselect
        options={[
          { value: '', label: 'All' },
          ...filters.address.map(el => ({ value: el, label: el }))
        ]}
        selectedOption={{ value: shell_host_address_filter }}
        handleSelect={({ value }) => onChange('shell_host_address_filter', value)}
        label={
          <span className="job-filter-select-label-wrap">
            <SvgIcon>
              <path d={mdiDatabase} />
            </SvgIcon>
            <span className="job-filter-select-label">Server</span>
          </span>
        }
      />
      {!excludeStatus && (
        <MUIselect
          selectedOption={{ value: status }}
          options={[
            { value: '', label: 'All' },
            ...filters.status.map(el => ({ value: el, label: el }))
          ]}
          handleSelect={({ value }) => onChange('status', value)}
          label={
            <span className="job-filter-select-label-wrap">
              <SvgIcon>
                <path d={mdiCircleSlice5} />
              </SvgIcon>
              <span className="job-filter-select-label">Status</span>
            </span>
          }
        />
      )}
    </div>
  );
};
Filters.propTypes = {
  filters: PropTypes.object.isRequired,
  selections: PropTypes.object.isRequired,
  onChange: PropTypes.func.isRequired,
  excludeStatus: PropTypes.bool
};

export const mapStateToProps = state => ({
  filters: getJobsFiltersOptions(state)
});

export default connect(mapStateToProps)(Filters);
