import React, { useCallback } from 'react';
import { bool, func, node, shape, object, string } from 'prop-types';
import classnames from 'classnames';

import Avatar from '@material-ui/core/Avatar';

import './AvatarCard.local.scss';

/* eslint-disable jsx-a11y/no-noninteractive-tabindex */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */

export const AvatarCard = ({ label, avatar = {}, selected, data, onSelect, action, ...props }) => {
  const focusable = {
    ...(onSelect && {
      onFocus: useCallback(
        event => {
          event.stopPropagation();
          onSelect(data);
        },
        [onSelect, data]
      ),
      tabIndex: props.disabled ? -1 : 0
    })
  };

  return (
    <div
      {...focusable}
      className={classnames('avatar-card', { selected })}
      data-qa={`avatar-card-of-${label}`}
      {...props}
    >
      <Avatar className="card-avatar" alt={label} src={avatar.src}>
        {avatar.initial || avatar.fallback}
      </Avatar>
      <span className="card-label">{label}</span>
      {action && (
        <span onClick={containEvent} className="action-slot">
          {action}
        </span>
      )}
    </div>
  );
};

function containEvent(e) {
  e.preventDefault();
  e.stopPropagation();
}

AvatarCard.propTypes = {
  label: node,
  selected: bool,
  disabled: bool,
  avatar: shape({
    src: string,
    fallback: node
  }),
  data: object,
  onSelect: func,
  action: node
};
