import React, { useState, useCallback } from 'react';
import { useHistory } from 'react-router-dom';
import { object, func, bool } from 'prop-types';
import { connect } from 'react-redux';

import { Switch, Center } from 'shared/components';
import { Paper, Section } from 'shared/components/layout';
import { Button } from 'shared/components/controls';
import { Result } from 'shared/components/overlays/Result/Result';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Loader } from 'shared/components/loaders';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiTrashCan } from '@mdi/js';

import { PrivilegesContainer } from './containers/Privileges';
import { UserGroupsContainer } from './containers/UserGroups';

import './UserView.local.scss';
import { removeUserThunk } from '../../../store/thunks';
import { EditUserDetailsContainer } from './containers/EditUserDetails';
import { EditUserInfoContainer } from './containers/EditUserInfo';

export const UserView = ({ user, removeUser, removing }) => {
  const history = useHistory();

  const [result, setResult] = useState('attempt');
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: useCallback(() => {
      setOpen(true);
    }, []),
    close: useCallback(() => {
      setResult('attempt');
      setOpen(false);
    }, [])
  };

  const deleteUser = useCallback(() => {
    removeUser(user.name)
      .then(() => {
        setResult('attempt');
        setOpen(false);
        history.push('/system/mas-accounts/users');
      })
      .catch(() => {
        setResult('attempt');
        setOpen(false);
        alert('Could not remove user!'); // eslint-disable-line
      });
  }, [user]);

  /* This needs to be written as a result check or 404 page for a user. */
  if (!user) {
    return <></>;
  }

  return (
    <>
      <Paper flex="column">
        <Paper.Header>
          <Paper.Title>{user.name}</Paper.Title>
        </Paper.Header>
        <Paper.Content>
          <Section>
            <Section.Title>Global Privileges</Section.Title>
            <PrivilegesContainer />
          </Section>
          <Section>
            <Section.Title>Member of Groups</Section.Title>
            <UserGroupsContainer />
          </Section>
        </Paper.Content>
        <Paper.Footer>
          <Paper.Footer.Left>
            <Button color="secondary" disabled={removing} onClick={modalActions.open}>
              Remove
            </Button>
            <EditUserDetailsContainer />
            <EditUserInfoContainer />
          </Paper.Footer.Left>
        </Paper.Footer>
      </Paper>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '400px',
            maxHeight: '368px',
            maxWidth: '65vw'
          }}
        >
          <ModalHeader
            title="Remove user"
            icon={
              <SvgIcon>
                <path d={mdiTrashCan} />
              </SvgIcon>
            }
          />
          <Switch value={result}>
            <Switch.Case value="attempt">
              <Paper.Content scrollable={false}>
                <Result type="fail">
                  <BodyText>
                    Are you sure you want to remove user {user.name}? This could affect the way the
                    user will be able to interact with the app.
                  </BodyText>
                </Result>
              </Paper.Content>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    color="primary"
                    variant="contained"
                    onClick={() => {
                      setResult('removing');
                      deleteUser();
                    }}
                  >
                    Yes
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="removing">
              <Paper.Content scrollable={false}>
                <Center>
                  <Loader>Removing...</Loader>
                </Center>
              </Paper.Content>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
};

UserView.propTypes = {
  user: object,
  removeUser: func,
  removing: bool
};

export const ConnectedUserView = connect(
  state => ({
    user: state.usersManager.users.selected,
    removing: state.usersManager.flags.user.removing
  }),
  dispatch => ({
    removeUser: userName => dispatch(removeUserThunk(userName))
  })
)(UserView);

function BodyText({ children }) {
  return (
    <h2
      style={{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: '14px',
        lineHeight: '20px',
        letterSpacing: '0.25px',
        marginTop: '24px'
      }}
    >
      {children}
    </h2>
  );
}
