import React from 'react';
import PropTypes from 'prop-types';
import { StorageSidebar, ObjectsSidebar } from './Sidebar';
import { StorageViewer, ObjectsViewer } from './Viewer';

import './root.local.scss';

export const DbObjects = ({ type, data, list, dbName, selectedItem, setItem, loadingFlags, invalidObjects }) => {
  const { loadingPoints, loadingDetails, loading } = loadingFlags;
  if (type === 'storage') {
    return (
      <div className="db-objects-tabs">
        <StorageSidebar dbName={dbName} isLoading={loadingPoints} list={list} selectedItem={selectedItem} setItem={setItem} />
        <StorageViewer data={data} isLoading={loadingDetails} />
      </div>
    );
  }
   return (
      <div className="db-objects-tabs">
        <ObjectsSidebar dbName={dbName} invalidObjects={invalidObjects} isLoading={loading} list={list} selectedItem={selectedItem} setItem={setItem} />
        <ObjectsViewer data={data} isLoading={loading} />
      </div>
    );
};

DbObjects.propTypes = {
  type: PropTypes.string,
  data: PropTypes.object,
  list: PropTypes.array,
  selectedItem: PropTypes.string,
  setItem: PropTypes.func,
  dbName: PropTypes.string
};
