import React from 'react';
import * as PropTypes from 'prop-types';
import moment from 'moment';

import { Link } from 'react-router-dom';

import AccountCircleOutlinedIcon from '@material-ui/icons/AccountCircleOutlined';
import { mdiClockOutline, mdiHexagonMultipleOutline, mdiDatabase } from '@mdi/js';
import SvgIcon from '@material-ui/core/SvgIcon';
import StorageIcon from '@material-ui/icons/Storage';

import { JobStatus } from '../job-status';

import './job-item.local.scss';

export class JobItem extends React.PureComponent {
  render() {
    return (
      <Link to={`/jobs/job/?invocation=${this.props.job.get('uuid')}`}>
        <div className={`${this.props.className}`}>
          <div className="status-icon">
            <JobStatus status={this.props.job.get('status')} />
          </div>
          <div className="job-metadata">
            <div className="first-row">
              <div className="name" title={this.props.job.get('process')}>
                {this.formatName()}
              </div>
            </div>
            <div className="second-row">
              <div className="metadata-element time">
                <SvgIcon>
                  <path d={mdiClockOutline} />
                </SvgIcon>
                {moment(this.props.job.get('date_invoke')).format('D MMM YYYY, HH:mm:ss')}
              </div>
              {this.props.job.get('user_owner') && (
                <div className="metadata-element run-by">
                  <AccountCircleOutlinedIcon />
                  {this.props.job.get('user_owner')}
                </div>
              )}
              {this.props.job.get('connections') &&
                this.props.job.get('connections').get('dbms') &&
                this.props.job
                  .get('connections')
                  .get('dbms')
                  .get('name') && (
                  <div className="metadata-element dbms">
                    <SvgIcon>
                      <path d={mdiHexagonMultipleOutline} />
                    </SvgIcon>
                    {this.props.job
                      .get('connections')
                      .get('dbms')
                      .get('name')}
                  </div>
                )}
              {this.props.job.get('connections') &&
                this.props.job.get('connections').get('shell') &&
                this.props.job
                  .get('connections')
                  .get('shell')
                  .get('host_address') && (
                  <div className="metadata-element server">
                    <StorageIcon />
                    {this.props.job.get('connections') &&
                      this.props.job.get('connections').get('shell') &&
                      this.props.job
                        .get('connections')
                        .get('shell')
                        .get('host_address')}
                  </div>
                )}
              {this.props.job.get('connections') &&
                this.props.job.get('connections').get('dbms') &&
                this.props.job
                  .get('connections')
                  .get('dbms')
                  .get('protocol_special') &&
                this.props.job
                  .get('connections')
                  .get('dbms')
                  .get('protocol_special')
                  .get('database') && (
                  <div className="metadata-element database">
                    <SvgIcon>
                      <path d path={mdiDatabase} />
                    </SvgIcon>
                    {this.props.job.get('connections') &&
                      this.props.job.get('connections').get('dbms') &&
                      this.props.job
                        .get('connections')
                        .get('dbms')
                        .get('protocol_special')
                        .get('database')}
                  </div>
                )}
            </div>
          </div>
        </div>
      </Link>
    );
  }

  formatName = () => {
    let name = this.props.job.get('process');
    const nameContainerWidth =
      (document.getElementById('jobs-content') &&
        document.getElementById('jobs-content').offsetWidth -
          (5 / 100) * document.getElementById('jobs-content').offsetWidth) ||
      600; // time container
    const starPath = <span className="word cutted">.../</span>;
    const formattedName = [];
    name = name.split('.');
    for (let i = 0; i < name.length; i++) {
      if (i < name.length - 1) {
        formattedName.push(
          <span key={i}>
            <span className="word">{name[i]}</span>.
          </span>
        );
      } else {
        formattedName.push(
          <span key={i} className="word">
            {name[i]}
          </span>
        );
      }
    }

    const canvas =
      this.formatName.canvas || (this.formatName.canvas = document.createElement('canvas'));
    const context = canvas.getContext('2d');
    let metrics = context.measureText(formattedName);

    if (metrics.width > nameContainerWidth) {
      // add '../' as the first word in the name
      formattedName.unshift(starPath);
    }

    while (metrics.width > nameContainerWidth) {
      // remove first word from the name (it is ../)
      formattedName.shift();
      // remove first word form the actual name
      formattedName.shift();
      // add '../' as the first word in the name
      formattedName.unshift(starPath);
      // recheck width
      metrics = context.measureText(formattedName);
    }

    return formattedName;
  };
}

JobItem.propTypes = {
  className: PropTypes.string,
  job: PropTypes.object.isRequired
};

JobItem.defaultProps = {
  className: 'job-item'
};
