import * as Yup from 'yup';

export const mergeValidations = (...callbacks) => value => {
  let error;

  callbacks.forEach(provided => {
    if (provided && typeof provided === 'function') {
      error = provided(value);
    }
  });

  return error;
};

export const checkRequired = value => {
  try {
    Yup.string()
      .nullable()
      .required('Field is required')
      .validateSync(value);
  } catch (e) {
    return e.message;
  }
};

export const checkRequiredNotNullable = value => {
  try {
    Yup.string()
      .required('Field is required')
      .validateSync(value);
  } catch (e) {
    return e.message;
  }
};

export const checkEmail = value => {
  try {
    Yup.string()
      .nullable()
      .email('Invalid email format.')
      .validateSync(value);
  } catch (e) {
    return e.message;
  }
};

export const checkPhoneNumber = value => {
  try {
    Yup.string()
      .nullable()
      .matches(/^(?:\+?1)? *(?:\( *[0-9]{3} *\)|[0-9]{3}(?: *-|)) *[0-9]{3} *- *[0-9]{4}$/, {
        excludeEmptyString: true,
        message: 'A valid phone number of form xxx-xxx-xxxx is required.'
      })
      .validateSync(value);
  } catch (e) {
    return e.message;
  }
};

export const checkUnsignedInt = value => {
  try {
    Yup.string()
      .nullable()
      .matches(/^[-]!$/, {
        excludeEmptyString: true,
        message: 'The parameter must be unsigned.'
      })
      .validateSync(value);
  } catch (e) {
    return e.message;
  }
};

export const checkName = value => {
  if (!/^(?:[a-z_][a-z0-9_\\.]*)$/i.test(value) || (value && value.substr(-1) === '.')) {
    return 'A valid MAS relative name is required.';
  }
};

export const checkFirstLetter = value => {
  try {
    Yup.string()
      .nullable()
      .matches(/^[a-zA-Z].*/, {
        excludeEmptyString: true,
        message: 'First character must be a letter'
      })
      .validateSync(value);
  } catch (e) {
    return e.message;
  }
};

export const noop = () => {};

export default {
  merge: mergeValidations,
  required: checkRequired,
  required_not_null: checkRequiredNotNullable,
  email: checkEmail,
  phone: checkPhoneNumber,
  unsigned_int: checkUnsignedInt,
  unsigned_float: noop,
  signed_float: noop,
  masName: checkName,
  firstCharLetter: checkFirstLetter
};
