import React, { useState } from 'react';

import { Button } from 'shared/components/controls';
import { ScheduleModal } from './components/ScheduleModal/ScheduleModal';

export const Schedule = ({ invocation }) => {
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };

  const onClickHandler = () => {
    setOpen(true);
  };

  const btnLabel = invocation ? 'Schedule' : 'New Schedule';

  return (
    <>
      <Button
        onClick={() => {
          onClickHandler();
        }}
      >
        {btnLabel}
      </Button>
      <ScheduleModal open={open} actions={modalActions} invocation={invocation} />
    </>
  );
};
