import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { changeStateThunk, loadSessionsThunk } from '../../store/thunks';
import { Paper } from 'shared/components/layout';
import { Table, LightTooltip, Center } from 'shared/components';
import { NavigationTab } from '../../components/NavigationTab/NavigationTab';
import { Button } from 'shared/components/controls';
import { FormattedMessage } from 'react-intl';
import CircularProgress from '@material-ui/core/CircularProgress';
// import SvgIcon from '@material-ui/core/SvgIcon';
// import Icon from '@mdi/react';
// import { mdiViewCompact } from '@mdi/js';
import { getFullPath, getSortedSessions } from '../../store/selectors';

import './Sessions.local.scss';

const columns = [
  { label: 'Serial #' },
  { label: 'Username' },
  { label: 'Status' },
  { label: 'Machine' },
  { label: 'SID' },
  { label: 'Logon time', sortKey: 'LOGON_TIME' },
  { label: 'Program' },
  { label: 'Type' }
  // { label: '' } -> disabled view details button for now...
];

const CellWithTooltip = ({ name }) => {
  if (name && name.length > 16) {
    return (
      <LightTooltip placement="bottom" title={<span>{name}</span>}>
        <span className="session-cell ellipsis">
          {name}
        </span>
      </LightTooltip>
    );
  }
  return <span className="session-cell">{name}</span>;
};

const rowRenderer = (row, index) => (
  <Table.Row hover key={`${row.sid}_${index}`} className="session-row">
    <Table.Cell><CellWithTooltip name={row['SERIAL#']} /></Table.Cell>
    <Table.Cell><CellWithTooltip name={row.USERNAME} /></Table.Cell>
    <Table.Cell><CellWithTooltip name={row.STATUS} /></Table.Cell>
    <Table.Cell><CellWithTooltip name={row.MACHINE} /></Table.Cell>
    <Table.Cell><CellWithTooltip name={row.SID} /></Table.Cell>
    <Table.Cell><CellWithTooltip name={row.LOGON_TIME} /></Table.Cell>
    <Table.Cell><CellWithTooltip name={row.PROGRAM} /></Table.Cell>
    <Table.Cell><CellWithTooltip name={row.TYPE} /></Table.Cell>
    { /* <Table.Cell padding="none" className="view-details">
      <LightTooltip placement="bottom" title={<span>View Details</span>}>
        <SvgIcon>
          <Icon path={mdiViewCompact} />
        </SvgIcon>
      </LightTooltip>
    </Table.Cell> */ }
  </Table.Row>
);

const TableRenderer = ({ isLoading, sessions, noDataMessage, tableProps }) => {
  if (isLoading) {
    return (
      <Center>
        <CircularProgress />
      </Center>
    );
  }
  if (sessions.length === 0) {
    return (
      <Center>
        { noDataMessage }
      </Center>
    );
  }
  return (
    <Table {...tableProps}>
      { sessions.map(rowRenderer) }
    </Table>
  );
};

export const Sessions = (props) => {
  const {
    level,
    sessions,
    isLoading,
    getSessions,
    fullPath,
    changeTab
  } = props;

  const [sortBehavior, setSortBehavior] = useState({ sortKey: 'LOGON_TIME', order: 'asc' });
  const changeSort = (sortKey, order) => () => setSortBehavior({ sortKey, order });
  const getSessionsData = () => {
    getSessions({
      sort: sortBehavior,
      fullPath,
      page_size: 2147483645
    });
  };
  const tableProps = { changeSort, columns, sortBehavior };

  useEffect(getSessionsData, [sortBehavior]);

  return (
    <>
      <Paper.Header>
        <NavigationTab active={level} onClick={changeTab} />
      </Paper.Header>
      <Paper.Content>
        <TableRenderer isLoading={isLoading} sessions={sessions} tableProps={tableProps} noDataMessage="No Sessions Available" />
      </Paper.Content>
      <Paper.Footer>
        <Button className="qa-refresh-btn align-right" onClick={getSessionsData}>
          <FormattedMessage id="general.refresh" />
        </Button>
      </Paper.Footer>
    </>
  );
};

Sessions.propTypes = {
  level: PropTypes.string.isRequired,
  isLoading: PropTypes.bool,
  fullPath: PropTypes.string.isRequired,
  changeTab: PropTypes.func
};

export const ConnectedSessions = connect(
  state => ({
    sessions: getSortedSessions(state),
    level: state.explorer.state.level,
    isLoading: state.explorer.flags.sessions.loading,
    fullPath: getFullPath(state)
  }),
  dispatch => ({
    changeTab: newState => dispatch(changeStateThunk({ level: newState })),
    getSessions: filters => dispatch(loadSessionsThunk(filters))
  })
)(Sessions);
