import React from 'react';
import * as PropTypes from 'prop-types';
import { SingleDatePicker } from 'react-dates';
import { mdiCalendarBlank, mdiChevronLeft, mdiChevronRight } from '@mdi/js';
import Icon from '@mdi/react';
import SvgIcon from '@material-ui/core/SvgIcon';

import './single-date-picker.local.scss';

export class SingleDatePickerComponent extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      focused: true,
    };
  }

  render() {
    const { date, onDateChange, ...others } = this.props;

    return (
      <div className="date-picker-container">
        <SingleDatePicker
          date={date}
          onDateChange={onDateChange}
          focused={this.state.focused}
          onFocusChange={({ focused }) => this.setState({ focused })}
          id="date-picker"
          numberOfMonths={1}
          customInputIcon={<SvgIcon><Icon path={mdiCalendarBlank} /></SvgIcon>}
          placeholder="Select Date"
          isOutsideRange={() => false}
          readOnly
          hideKeyboardShortcutsPanel
          displayFormat="D MMM YYYY"
          /** TODO: @Razvan, look at this please. */
          navPrev={<div className="DayPickerNavigation_button DayPickerNavigation_button_1 DayPickerNavigation_button__default DayPickerNavigation_button__default_2 DayPickerNavigation_button__horizontal DayPickerNavigation_button__horizontal_3 DayPickerNavigation_button__horizontalDefault DayPickerNavigation_button__horizontalDefault_4 DayPickerNavigation_leftButton__horizontalDefault DayPickerNavigation_leftButton__horizontalDefault_5 DayPicker-nav-icon"><SvgIcon><Icon path={mdiChevronLeft} size={6} /></SvgIcon></div>}
          navNext={<div className="DayPickerNavigation_button DayPickerNavigation_button_1 DayPickerNavigation_button__default DayPickerNavigation_button__default_2 DayPickerNavigation_button__horizontal DayPickerNavigation_button__horizontal_3 DayPickerNavigation_button__horizontalDefault DayPickerNavigation_button__horizontalDefault_4 DayPickerNavigation_rightButton__horizontalDefault DayPickerNavigation_rightButton__horizontalDefault_5 DayPicker-nav-icon"><SvgIcon><Icon path={mdiChevronRight} size={6} /></SvgIcon></div>}
          {...others}
        />
      </div>
    );
  }
}

SingleDatePickerComponent.propTypes = {
  date: PropTypes.object,
  onDateChange: PropTypes.func.isRequired,
};

SingleDatePickerComponent.defaultProps = {
  date: null,
};

export default SingleDatePickerComponent;
