import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';

import { useSelector, useDispatch } from 'react-redux';

import { actions } from 'shared/store/actions';
import { loadAllAccountsThunk } from 'explorer/store/thunks';
import { globalFiltersSelector } from 'shared/store/state';

export const JobsInvocations = ({ children }) => {
  const dispatch = useDispatch();
  const getJobs = data => {
    dispatch(actions.getJobs.dispatchLoading(data));
    dispatch(actions.getConnections.dispatchLoading(data));
  };
  const globalFilters = useSelector(globalFiltersSelector);

  React.useEffect(() => {
    const days = moment
      .unix(globalFilters.get('endDate'), 'YYYY-MM-DD')
      .diff(moment.unix(globalFilters.get('startDate'), 'YYYY-MM-DD'), 'days');

    getJobs({
      date_invoke: moment.unix(globalFilters.get('startDate')).format('YYYY-MM-DD'),
      period: days + 1,
      page_size: 2147483645
    });
    dispatch(loadAllAccountsThunk({ from: 'jobs' }));
  }, []);

  return children;
};
JobsInvocations.propTypes = {
  children: PropTypes.any.isRequired
};
