import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Formik, Form } from 'formik';
import _, { isEmpty, pickBy } from 'lodash';

import { fetchEntity } from '../actions';
import { Switch } from 'shared/components';
import { Button } from 'shared/components/controls';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Result } from 'shared/components/overlays/Result/Result';
import { SubmitButton } from 'shared/components/form/SubmitButton';
import { Paper, Section, Row, Column } from 'shared/components/layout';
import { NavigationTab } from 'shared/components/layout/NavigationTab/NavigationTab';
import { Text } from 'shared/components/form/editors/Generic';

import { ProcessParameters } from 'job-process/job-parameters';
import { Notifications } from 'job-process/containers/notifications/notifications';

import { patchSchedule, getSchedule } from 'shared/api/schedule';
import { getProcess } from 'shared/api/process';

import SvgIcon from '@material-ui/core/SvgIcon';
import EventNoteIcon from '@material-ui/icons/EventNote';

import { hasWritePrivileges } from '../utils/privileges';

const ActionsSchedule = ({ entity }) => {
  const isDisabled = entity?.meta?.appearance === 'Greyed';
  const canWrite = hasWritePrivileges(entity);

  if (!canWrite) {
    return null;
  }

  return (
    <>
      <ConnectedEditSchedule schedule={entity} isDisabled={isDisabled} />
    </>
  );
};

export default ActionsSchedule;

export const ConnectedEditSchedule = connect(null, {
  request: fetchEntity
})(EditSchedule);

function EditSchedule({ schedule, isDisabled, request }) {
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
      setResult('');
    }
  };
  const [result, setResult] = useState('');
  const [scheduleDetails, setScheduleDetails] = useState({});
  const [processDetails, setProcessDetails] = useState({});
  const [tab, setTab] = useState('schedule');

  useEffect(() => {
    if (!_.isEmpty(scheduleDetails) && !_.isEmpty(processDetails)) {
      setOpen(true);
    }
  }, [scheduleDetails, processDetails]);

  const onClickHandler = () => {
    /** TODO cover the case when one (or both) of the below calls fail or no data is returned from them */

    getSchedule(schedule.name)
      .then(data => {
        if (data.data.data && data.data.data.schedules && data.data.data.schedules[0]) {
          setScheduleDetails(data.data.data.schedules[0]);
        }
      })
      .catch(error => {
        console.log('[get process] error getting process details: ', error); // eslint-disable-line
      });

    getProcess(schedule.process)
      .then(data => {
        if (data.data.data && data.data.data.processes && data.data.data.processes[0]) {
          setProcessDetails(data.data.data.processes[0]);
        }
      })
      .catch(error => {
        console.log('[get process] error getting process details: ', error); // eslint-disable-line
      });
  };

  return (
    <>
      <Button
        classes={{ root: 'entity-button' }}
        onClick={() => {
          onClickHandler();
        }}
        disabled={isDisabled}
      >
        Edit
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '800px',
            minHeight: '85vh',
            maxWidth: '85vw',
            maxHeight: '85vh'
          }}
        >
          <ModalHeader
            title="Edit Schedule"
            icon={
              <SvgIcon>
                <EventNoteIcon />
              </SvgIcon>
            }
          />
          <Switch value={result}>
            <Switch.Case value="success">
              <Result type="success">
                <Result.Text>Schedule was edited successfully!</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                    }}
                  >
                    Done
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="fail">
              <Result type="fail">
                <Result.Text>
                  An error occurred and the new schedule could not be added.
                </Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>

            <Switch.Case value="">
              <Paper.Header>
                <ViewTabs active={tab} changeTab={setTab} />
              </Paper.Header>
              <Switch value={tab}>
                <Switch.Case value="schedule">
                  <ScheduleForm
                    process={processDetails}
                    schedule={scheduleDetails}
                    setResult={setResult}
                    request={request}
                  />
                </Switch.Case>
                <Switch.Case value="documentation">
                  <Paper.Content>
                    <Documentation process={processDetails} />
                  </Paper.Content>
                </Switch.Case>
              </Switch>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
}

function ViewTabs({ active, changeTab }) {
  const tabs = [
    { label: 'Schedule', value: 'schedule', active: true },
    { label: 'Documentation', value: 'documentation' }
  ];

  return <NavigationTab active={active} onClick={changeTab} tabs={tabs} />;
}

const ScheduleForm = ({ schedule, setResult, request }) => {
  const derivedMonitor = {
    enabled: !!schedule?.parameters?.events,
    events: schedule?.parameters?.events || 0,
    users: schedule?.parameters?.users || []
  };
  const [monitor, setMonitor] = useState(derivedMonitor);

  const splittedName = schedule.name.split('.');
  const name = splittedName[splittedName.length - 1];

  const extraValidations = values => {
    const { t_min, t_hr, t_day, t_mon, t_wkd, sch_name } = values;
    const errors = {};

    if (sch_name.includes('.')) {
      errors.sch_name = 'Name can not include "."';
    }

    // at least one of the fields need to have a numeric value
    if (
      Number.isNaN(Number(t_min)) &&
      Number.isNaN(Number(t_hr)) &&
      Number.isNaN(Number(t_day)) &&
      Number.isNaN(Number(t_mon)) &&
      Number.isNaN(Number(t_wkd))
    ) {
      errors.t_min = 'Expected a numeric value';
      errors.t_hr = 'Expected a numeric value';
      errors.t_day = 'Expected a numeric value';
      errors.t_mon = 'Expected a numeric value';
      errors.t_wkd = 'Expected a numeric value';
    }

    if (parseInt(t_min, 10) < 0 || parseInt(t_min, 10) > 60) {
      errors.t_min = 'Value must be between 0 and 60';
    }
    if (parseInt(t_hr, 10) < 0 || parseInt(t_hr, 10) > 24) {
      errors.t_hr = 'Value must be between 0 and 24';
    }
    if (parseInt(t_day, 10) < 1 || parseInt(t_day, 10) > 31) {
      errors.t_day = 'Value must be between 1 and 31';
    }
    if (parseInt(t_mon, 10) < 1 || parseInt(t_mon, 10) > 12) {
      errors.t_mon = 'Value must be between 1 and 12';
    }
    if (parseInt(t_wkd, 10) < 1 || parseInt(t_wkd, 10) > 7) {
      errors.t_wkd = 'Value must be between 1 and 7';
    }

    return errors;
  };

  return (
    <>
      <Formik
        initialValues={{}}
        validate={extraValidations}
        onSubmit={handleSubmit.bind(this, schedule, monitor, setResult, request)}
      >
        {() => {
          return (
            <>
              <Paper.Content>
                <Form>
                  <Section>
                    <Section.Header>
                      <Section.Title>Process</Section.Title>
                    </Section.Header>
                    <Section.Content>
                      <Row>
                        <Text
                          name="process"
                          label="Process"
                          description={schedule.description}
                          value={schedule.process}
                          disabled
                        />
                      </Row>
                    </Section.Content>
                  </Section>

                  <Section>
                    <Section.Header>
                      <Section.Title>Schedule</Section.Title>
                    </Section.Header>
                    <Section.Content>
                      <Row>
                        <Text name="sch_name" label="Name" value={name} required />
                      </Row>
                      <Row>
                        <Text
                          name="sch_description"
                          label="Description"
                          value={schedule.description}
                          multiline
                          required
                        />
                      </Row>
                      <Row>
                        <Column style={{ maxWidth: '20%', width: '20%' }}>
                          <Text name="t_min" label="T_min" value={schedule.t_min} />
                        </Column>
                        <Column style={{ maxWidth: '20%', width: '20%' }}>
                          <Text name="t_hr" label="T_hr" value={schedule.t_hr} />
                        </Column>
                        <Column style={{ maxWidth: '20%', width: '20%' }}>
                          <Text name="t_day" label="T_Day" value={schedule.t_day} />
                        </Column>
                        <Column style={{ maxWidth: '20%', width: '20%' }}>
                          <Text name="t_mon" label="T_mon" value={schedule.t_mon} />
                        </Column>
                        <Column style={{ maxWidth: '20%', width: '20%' }}>
                          <Text name="t_wkd" label="T_wkd" value={schedule.t_wkd} />
                        </Column>
                      </Row>
                    </Section.Content>
                  </Section>

                  <ProcessParameters recieved={schedule.parameters} editable />
                  <Section>
                    <Section.Header>
                      <Section.Title>Notifications</Section.Title>
                    </Section.Header>
                    <Section.Content>
                      <Notifications
                        name="notifications"
                        recievedData={derivedMonitor}
                        onApply={n_state => {
                          if (n_state.events === 0 || isEmpty(n_state.users)) {
                            setMonitor({
                              enabled: false
                            });
                          }
                          setMonitor({
                            enabled: true,
                            ...n_state
                          });
                        }}
                      />
                    </Section.Content>
                  </Section>
                </Form>
              </Paper.Content>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <SubmitButton variant="contained">Save</SubmitButton>
                </Paper.Footer.Right>
              </Paper.Footer>
            </>
          );
        }}
      </Formik>
    </>
  );
};

async function handleSubmit(schedule, monitor, setResult, request, values) {
  const {
    sch_name,
    sch_description,
    process,
    t_min,
    t_hr,
    t_day,
    t_mon,
    t_wkd,
    ...params_with_maybe_replvars
  } = values;
  const body = {
    rename: sch_name,
    description: sch_description,
    process,
    t_min: t_min || null,
    t_hr: t_hr || null,
    t_day: t_day || null,
    t_mon: t_mon || null,
    t_wkd: t_wkd || null
  };

  const { parameters } = extractReplVars(params_with_maybe_replvars);

  function rejector(value, key) {
    if (key === 'parameters') {
      return false;
    }

    if (!value) {
      return false;
    }

    if (value === 'None') {
      return false;
    }

    if (isEmpty(value)) {
      return false;
    }

    return true;
  }

  if (monitor.enabled) {
    /**
     * Is monitoring a process
     */
    body.process = 'mas.sys.invocation.notify.create';
    body.parameters = {
      name: process,
      parameters: pickBy(parameters, rejector),
      events: monitor.events,
      users: monitor.users
    };
  } else {
    body.parameters = pickBy(parameters, rejector);
  }

  patchSchedule(schedule.name, body)
    .then(() => {
      setResult('success');
      request('schedule');
    })
    .catch(error => {
      console.log('[edit schedule] edit schedule error: ', error); // eslint-disable-line
      setResult('fail');
    });
}

function extractReplVars(values) {
  const keys = Object.keys(values);

  const result = keys.reduce(
    (extracted, key) => {
      if (key.startsWith('replvar--')) {
        const [index, field_name] = key.replace('replvar--', '').split('--');

        const entry = extracted.replvars[index];

        // ignore deflt propery if is an empty string
        if (field_name === 'deflt' && values[key] === '') {
          return extracted;
        }

        if (entry) {
          if (field_name === 'deflt') {
            entry[field_name] = `str('"'${values[key]}'"')`;
          } else {
            entry[field_name] = values[key];
          }
        } else {
          if (field_name === 'deflt') {
            extracted.replvars.push({ [field_name]: `str('"'${values[key]}'"')` });
          } else {
            extracted.replvars.push({ [field_name]: values[key] });
          }
        }

        return extracted;
      }

      extracted.parameters[key] = values[key];

      return extracted;
    },
    { replvars: [], parameters: {} }
  );

  return { parameters: { ...result.parameters, replvars: cleanUpReplVars(result.replvars) } };
}

function cleanUpReplVars(replvars) {
  return replvars.filter(p => true).map((p, index) => ({ ...p, meta: { position: index + 3 } }));
}

const Documentation = ({ process }) => {
  return (
    <Section>
      <Section.Header>
        <Section.Title>{process.name}</Section.Title>
        <Section.Info>{process.description}</Section.Info>
      </Section.Header>
      {process.parameters && process.parameters.length > 0 && (
        <Section.Content>
          <h2>
            <b>Parameters:</b>
          </h2>
          <div>
            {process.parameters.map(parameter => {
              return (
                <React.Fragment key={parameter.name}>
                  <h3>
                    {parameter.name} ({parameter.data_type})
                  </h3>
                  <Section.Info>{parameter.description}</Section.Info>
                </React.Fragment>
              );
            })}
          </div>
        </Section.Content>
      )}
    </Section>
  );
};
