import React, { useState, useEffect, useMemo } from 'react';
import { object, func } from 'prop-types';
import { connect } from 'react-redux';
import { produce } from 'immer';
import SvgIcon from '@material-ui/core/SvgIcon';
import Avatar from '@material-ui/core/Avatar';

import { mdiAccountGroup, mdiAccountMultiplePlus } from '@mdi/js';

import { CONSTANTS } from 'config';
import { RestClient } from 'shared/lib/rest';

import { Button, Checkbox } from 'shared/components/controls';

import { Paper } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';
import { Modal, ModalHeader, ModalFooter, Result } from 'shared/components/overlays';

import { SearchableList } from '../../../../components/SearchableList/SearchableList';

import { AvatarCard } from '../../../../components/AvatarCard/AvatarCard';

import { addUsersToGroupThunk } from '../../../store/thunks';

import './EditGroupUsers.local.scss';

export const EditGroupUsers = ({ group, addUsersToGroup }) => {
  const [open, setOpen] = useState(false);
  // could use memo and callback
  const openModal = () => {
    setOpen(true);
  };

  const closeModal = () => {
    setOpen(false);
  };

  const modalActions = {
    close: closeModal,
    open: openModal
  };

  /* eslint-disable jsx-a11y/click-events-have-key-events */
  return (
    <>
      <div tabIndex={0} role="button" className="edit-user-groups" onClick={openModal}>
        <AvatarCard
          label="Add user"
          avatar={{
            fallback: (
              <SvgIcon>
                <path d={mdiAccountMultiplePlus} />
              </SvgIcon>
            )
          }}
        />
      </div>
      <Modal open={open} actions={modalActions}>
        {({ aria, actions }) => (
          <Paper style={{ minWidth: '75vh', minHeight: '75vh' }}>
            <ModalHeader
              avatar={
                <Avatar className="generic-avatar" alt="edit-user-groups">
                  <SvgIcon>
                    <path d={mdiAccountGroup} />
                  </SvgIcon>
                </Avatar>
              }
              title="Add to Group"
              desc="Please select the users you want to add this group"
              aria={aria}
              onClose={actions.close}
            />
            <UsersSelection
              group={group}
              closeModal={closeModal}
              onAdd={selections => {
                return addUsersToGroup(group.name, selections);
              }}
            />
          </Paper>
        )}
      </Modal>
    </>
  );
};

EditGroupUsers.propTypes = {
  group: object,
  addUsersToGroup: func
};

export const EditGroupUsersContainer = connect(
  state => {
    const group = state.groups.selected;

    return { group };
  },
  dispatch => ({
    addUsersToGroup: (group, users) => dispatch(addUsersToGroupThunk({ group, users }))
  })
)(EditGroupUsers);

const UsersSelection = ({ group, closeModal, onAdd }) => {
  const [state, setState] = useState({
    selections: [],
    usersFetched: false,
    usersSubmitted: false,
    loading: true,
    result: undefined,
    all: [],
    showAll: false
  });

  const filtered = useMemo(() => {
    return state.showAll ? [...state.all] : state.all.filter(u => !group.users.includes(u.name));
  }, [state.all, state.showAll, group]);

  useEffect(() => {
    RestClient.get(`${CONSTANTS.BASE.ENDPOINTS.USERS}`)
      .then(({ data }) => {
        setState(prev =>
          produce(prev, draft => {
            draft.all = data.data.users;
            draft.usersFetched = true;
            draft.result = 'success';
            draft.loading = false;
          })
        );
      })
      .catch(error => {
        setState(prev =>
          produce(prev, draft => {
            draft.all = [];
            draft.error = error.messages;
            draft.usersFetched = true;
            draft.result = 'fail';
            draft.loading = false;
          })
        );
      });
  }, []);

  if (state.loading) {
    return (
      <Paper.Content>
        <Loader />
      </Paper.Content>
    );
  }

  if (state.usersFetched && (!state.all.length || state.result === 'fail')) {
    return (
      <>
        <Paper.Content>
          <Result type="fail">
            <Result.Text>Could not retrieve user list!</Result.Text>
          </Result>
        </Paper.Content>
        <ModalFooter>
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              closeModal();
            }}
          >
            Close
          </Button>
        </ModalFooter>
      </>
    );
  }

  if (state.usersFetched && state.usersSubmitted && state.result === 'success') {
    return (
      <>
        <Paper.Content>
          <Result type="success">
            <Result.Text>Added selected users to group succesfully!</Result.Text>
          </Result>
        </Paper.Content>
        <ModalFooter>
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              closeModal();
            }}
          >
            Confirm
          </Button>
        </ModalFooter>
      </>
    );
  }

  if (state.usersFetched && state.usersSubmitted && state.result === 'fail') {
    return (
      <>
        <Paper.Content>
          <Result type="fail">
            <Result.Text>Could not add users to group!</Result.Text>
          </Result>
        </Paper.Content>
        <ModalFooter>
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              closeModal();
            }}
          >
            Close
          </Button>
        </ModalFooter>
      </>
    );
  }

  return (
    <>
      <Paper.Content>
        <SearchableList
          items={filtered}
          filter={(item, query) => item.name.toLowerCase().includes(query.toLowerCase())}
          variant="row white"
        >
          {entry => {
            return (
              <div className="card-wrap" style={{ width: '178px' }}>
                <AvatarCard
                  onClick={() => {
                    setState(prev =>
                      produce(prev, draft => {
                        const selections = new Set(prev.selections);

                        if (selections.has(entry.name)) {
                          selections.delete(entry.name);
                        } else {
                          selections.add(entry.name);
                        }

                        draft.selections = [...selections];
                      })
                    );
                  }}
                  disabled={state.showAll ? group.users.includes(entry.name) : false}
                  selected={state.selections.includes(entry.name)}
                  label={entry.name}
                  avatar={{
                    initial: entry.name.charAt(0)
                  }}
                />
              </div>
            );
          }}
        </SearchableList>
      </Paper.Content>
      <ModalFooter>
        <ModalFooter.Left>
          <Checkbox
            checked={state.showAll}
            onInput={() => setState(prev => ({ ...prev, showAll: !prev.showAll }))}
            label="Show users that are members of this group"
          />
        </ModalFooter.Left>
        <ModalFooter.Right>
          <Button
            disabled={!state.selections.length}
            variant="contained"
            color="primary"
            onClick={() => {
              setState(prev =>
                produce(prev, draft => {
                  draft.loading = true;
                })
              );

              /**
                NOTE: (to self)

                This is lim 0 -> complete trash

                Can't use redux for snippets of this operation.
                The redux action that is required should only be to update final group entry at maximum.

                Either all ops are in redux, or none are.
               */
              onAdd(state.selections).then(response => {
                setState(prev =>
                  produce(prev, draft => {
                    draft.loading = false;
                    draft.usersSubmitted = true;
                    draft.result = response.result;
                    draft.error = response.messages;
                  })
                );
              });
            }}
          >
            Add
          </Button>
        </ModalFooter.Right>
      </ModalFooter>
    </>
  );
};

UsersSelection.propTypes = {
  group: object,
  onAdd: func,
  closeModal: func
};
