import { put, takeEvery } from '@redux-saga/core/effects';
import Cookie from 'js-cookie';

import { CONSTANTS } from 'config';
import { actions } from 'session/store/actions';
import { RestClient } from 'shared/lib/rest';

function* login(action) {
  try {
    Cookie.set('Authorization', Buffer.from(`${action.payload.username}:${action.payload.password}`).toString('base64'));
    RestClient.setBasicAuth();
    yield RestClient.post(CONSTANTS.BASE.ENDPOINTS.LOGIN);
    yield put(actions.login.dispatchSuccess());
    yield put(actions.checkAuthorization.dispatchLoading());
  } catch (errors) {
    if (!errors.response) {
      yield put(actions.login.dispatchFail([
        {
          message: 'Unknown Error. Please contact the administrator!',
        },
      ]));
      return;
    }

    if (errors.response.data.status === 422) {
      yield put(actions.login.dispatchFail(errors.response.data.error.details));
      return;
    }

    if (errors.response.data.status === 401) {
      yield put(actions.login.dispatchFail([
        {
          message: errors.response.statusText,
        },
      ]));

      return;
    }

    if (errors.response.data.status === 500 && (action.payload.password === '' || action.payload.username === '')) {
      yield put(actions.login.dispatchFail([
        {
          message: errors.response.statusText,
        },
      ]));

      return;
    }

    throw errors;
  }
}

export function* LoginEffect() {
  yield takeEvery(actions.login.getLoadingAction(), login);
}
