export const ConnectedExecutiveDashboard = connect(
  state => {
    return {
      isLoading: state.executiveDashboard.flags.dashboard.loading,
      callFailed: state.executiveDashboard.flags.dashboard.callFailed,
      selected: state.executiveDashboard.selectedServers.length,
      rawEntries: state.executiveDashboard.rawEntries
    };
  },
  dispatch => ({
    getExecutiveDashboard: () => dispatch(loadExecutiveDashboardThunk())
  })
)(ExecutiveDashboard);
