import React, { useState, useCallback, useMemo, useReducer } from 'react';
import { produce } from 'immer';
import { Section } from 'shared/components/layout';
import { Button } from 'shared/components/controls';

import FormGroup from '@material-ui/core/FormGroup';

import { Checkbox } from '../../controls/Checkbox/Checkbox';
import { Text, Select } from './Generic';

import { Ensure } from 'core/components/Ensure/Ensure';
import { ParameterLoader } from 'shared/components/loaders';

import { useFormikContext } from 'formik';

import { formatAccount } from './formatters';

import check from './field-level-validations';

export const RSAKey = props => {
  return <Text multiline {...props} />;
};

export const FQName = props => {
  return <Text multiline {...props} />;
};

export function QualifiedName(props) {
  const validations = useMemo(() => check.merge(check.masName, props.required ?? check.required), [
    props.required
  ]);

  return <Text validate={validations} {...props} type="text" />;
}

/**
 * TODO:
 * These should be retireved through a connection
 * Platforms are protocols associated for accounts
 * For now they can be hardcoded
 */
export const AccountSelect = ({ protocols = [], ...props }) => {
  const [showUrls, setShowUrls] = useState(false);
  const boxName = `${props.name}-show-urls`;

  const showToggle = useCallback(e => {
    setShowUrls(e.target.checked);
  }, []);

  return (
    <Ensure type="account" inlineLoader>
      {({ data: accounts }) => {
        let list = accounts;

        if (protocols && protocols.length) {
          list = accounts.filter(account => protocols.includes(account.protocol));
        }

        const options = list.map(account => ({
          label: showUrls ? formatAccount(account) : account.name,
          value: account.name
        }));

        /**
          TODO: filter by protocol
         */
        if (protocols.length) {
          accounts.filter(Boolean);
        }

        return (
          <FormGroup>
            <Text {...props} options={options} />
            <Checkbox
              size="small"
              id={boxName}
              name={boxName}
              onChange={showToggle}
              disabled={props.disabled}
              label="Show Account URLs"
            />
          </FormGroup>
        );
      }}
    </Ensure>
  );
};

export const PLATFORMS = [
  { label: 'All', value: 'All', tester: /^./ },
  { label: 'MariaDB', value: 'MariaDB', tester: /mariadb\./ },
  { label: 'MySQL', value: 'MySQL', tester: /mysql\./ },
  { label: 'Oracle', value: 'Oracle', tester: /oracle\./ },
  { label: 'PostgreSQL', value: 'PostgreSQL', tester: /pgsql\./ },
  {
    label: 'Microsoft SQL Server',
    value: 'Microsoft SQL Server',
    tester: /microsoft\.sqlserver\./
  },
  { label: 'Oracle RDS', value: 'Oracle RDS', tester: /.rds\.oracle\./ }
];

export const PlatformSelect = props => {
  return <Text type="text" value="All" name="platform" {...props} options={PLATFORMS} />;
};

export const ProcessSelect = props => {
  const { values } = useFormikContext();
  const platformValue = values?.platform ?? '';

  let platform = PLATFORMS[0];

  if (platformValue) {
    platform = PLATFORMS.find(p => p.value === platformValue);
  }

  return (
    <Ensure type="namespace" inlineLoader>
      <Ensure type="process" inlineLoader>
        {({ data }) => {
          let processes = data;

          if (platform && platform.value !== 'all') {
            processes = data.filter(process => platform.tester.test(process.name));
          }

          /** TODO, CHECK */
          processes = processes.filter(process => {
            if (processes.meta && process.meta.appearance) {
              return process.meta.appearance !== 'Hidden';
            }

            return true;
          });

          const OPTIONS = processes.map(item => ({
            value: item.name,
            label: item.name,
            disabled: item.meta && item.meta.appearance === 'Greyed'
          }));

          return <Text type="text" defaultValue="" name="process" {...props} options={OPTIONS} />;
        }}
      </Ensure>
    </Ensure>
  );
};

export const NamespaceSelect = props => {
  const { values } = useFormikContext();
  const defaultValue = values.namespace || '';

  return (
    <Ensure type="namespace" pipe>
      {({ status, data }) => {
        if (status === 'fetching' || !data) {
          return <ParameterLoader>Loading namespaces...</ParameterLoader>;
        }

        if (status === 'failed') {
          return <ParameterLoader>Failed to load namespaces.</ParameterLoader>;
        }

        let namespaces = data;

        namespaces = namespaces.filter(namespace => {
          if (namespace.meta && namespace.meta.appearance) {
            return namespace.meta.appearance !== 'Hidden';
          }

          return true;
        });

        const OPTIONS = namespaces.map(item => ({
          value: item.name,
          label: item.name,
          disabled: item.meta && item.meta.appearance === 'Greyed'
        }));

        return (
          <Text
            type="text"
            defaultValue={defaultValue}
            name="namespace"
            {...props}
            options={OPTIONS}
          />
        );
      }}
    </Ensure>
  );
};

export const FormPrototypeSelect = props => {
  return (
    <Ensure type="prototype" inlineLoader>
      {({ data: prototypes }) => {
        const list = prototypes || [];

        const options = list.map(prototype => ({
          label: prototype.name,
          value: prototype.name
        }));

        return <Text fullWidth {...props} description="" options={options} />;
      }}
    </Ensure>
  );
};

export const ModeSelect = props => {
  const { values } = useFormikContext();
  const defaultValue = values.mode || '';

  return (
    <Ensure type="type" pipe>
      {({ status, data }) => {
        if (status === 'fetching' || !data) {
          return <ParameterLoader>Loading type...</ParameterLoader>;
        }

        if (status === 'failed') {
          return <ParameterLoader>Failed to load types.</ParameterLoader>;
        }

        const modes = data.filter(type => type.name === 'mas0300.sys.account.privilege_mode')[0];

        if (modes.meta && modes.meta.appearance === 'Hidden') {
          return null;
        }

        const OPTIONS =
          modes.enumerations &&
          modes.enumerations.map(item => ({
            value: item.label,
            label: item.label,
            disabled: item.meta && item.meta.appearance === 'Greyed'
          }));

        return (
          <Text
            type="text"
            defaultValue={defaultValue}
            disabled={modes.meta && modes.meta.appearance === 'Greyed'}
            name="process"
            {...props}
            options={OPTIONS}
          />
        );
      }}
    </Ensure>
  );
};

function variadicListReducer(list, action) {
  switch (action.type) {
    case 'add_new_parameter':
      return produce(list, draft => {
        draft.push({ name: '', type: '', deflt: undefined, description: null, value: '' });
      });

    case 'remove_parameter':
      return produce(list, draft => {
        draft.shift();
      });

    default:
      return list;
  }
}

function useVariadicList(recieved) {
  const [list, dispatch] = useReducer(variadicListReducer, recieved?.replvars || []);

  return { list, dispatch };
}

export const Variadic = ({ recieved, disabled }) => {
  const { values } = useFormikContext();
  const displayDeftlDesc = selectedProcess !== 'vendita.oracle.rdbms.cli.sqlplus.execute';

  const { list, dispatch } = useVariadicList(recieved);

  const selectedProcess = values.process;

  return (
    <Section>
      <Section.Header>
        <Section.Title>replvars</Section.Title>
      </Section.Header>
      <Section.Content>
        <Ensure type="type">
          {({ data: datatypes }) => {
            const options = datatypes
              .filter(dataType => {
                if (
                  selectedProcess === 'vendita.oracle.rdbms.cli.sqlplus.script' ||
                  selectedProcess === 'mas0300.os.svc.db.data.sql.query_import' ||
                  selectedProcess === 'vendita.oracle.rdbms.cli.sqlplus.execute'
                ) {
                  return dataType.name === 'string' || dataType.name === 'integer';
                }
                return true;
              })
              .map(dataType => {
                return {
                  label: dataType.name,
                  value: dataType.name
                };
              });

            return (
              <>
                <div>
                  {list.map((item, index) => (
                    <React.Fragment key={index}>
                      <VariadicInstance
                        data={item}
                        index={index}
                        defltDesc={displayDeftlDesc}
                        types={options}
                        disabled={disabled}
                      />
                    </React.Fragment>
                  ))}
                </div>
                <div
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'flex-end'
                  }}
                >
                  {list.length > 0 && (
                    <Button
                      color="secondary"
                      onClick={() => {
                        dispatch({ type: 'remove_parameter' });
                      }}
                      disabled={disabled}
                    >
                      Remove
                    </Button>
                  )}
                  <Button
                    onClick={() => {
                      dispatch({ type: 'add_new_parameter' });
                    }}
                    disabled={disabled}
                  >
                    Add
                  </Button>
                </div>
              </>
            );
          }}
        </Ensure>
      </Section.Content>
    </Section>
  );
};

function VariadicInstance({ data = {}, index, defltDesc, types, disabled }) {
  const name_base = `replvar--${index}`;

  let parsedDeflt = data.deflt;

  if (data.deflt) {
    parsedDeflt = data.deflt.replace("str('\"'", '').replace("'\"')", '');
  }

  return (
    <>
      <Text
        style={{ marginBottom: '15px' }}
        label="Name"
        type="text"
        name={`${name_base}--name`}
        value={data.name}
        disabled={disabled}
        validate={check.merge(check.firstCharLetter, check.masName)}
        required
      />
      <Text
        style={{ marginBottom: '15px' }}
        label="Type"
        type="text"
        name={`${name_base}--type`}
        options={types}
        value={data.type}
        disabled={disabled}
        required
      />
      {defltDesc && (
        <>
          <Text
            style={{ marginBottom: '15px' }}
            label="Deflt"
            type="text"
            name={`${name_base}--deflt`}
            value={parsedDeflt}
            disabled={disabled}
            validate={check.firstCharLetter}
          />
          <Text
            style={{ marginBottom: '30px' }}
            label="Description"
            type="text"
            name={`${name_base}--description`}
            value={data.description}
            defaultValue={null}
            disabled={disabled}
            validate={check.firstCharLetter}
          />
        </>
      )}
      {!defltDesc && (
        <Text
          style={{ marginBottom: '15px' }}
          label="Value"
          type="text"
          name={`${name_base}--value`}
          value={data.value}
          disabled={disabled}
        />
      )}
    </>
  );
}
