import React from 'react';
import { connect } from 'react-redux';
import * as PropTypes from 'prop-types';

import { currentUserSelector } from 'session/store/state';
import { Navigation, UserMenu, Compile } from './components';

import MasLogo from '../../../../assets/images/mas_logo_white.svg';
import VenditaLogo from '../../../../assets/images/vendita_logo_white.svg';
import './sidebar.local.scss';

export class SidebarContainer extends React.PureComponent {
  render() {
    return (
      <div className={this.props.className}>
        <div className="logo-container">
          <img className="vendita-logo img-responsive" src={VenditaLogo} alt="Vendita logo" />
          <img className="mas-logo img-responsive" src={MasLogo} alt="MAS logo" />
        </div>

        <div className="nav-area">
          <Navigation />
        </div>
        <div className="bottom">
          <Compile />
          <UserMenu user={this.props.currentUser.get('name')} />
        </div>
      </div>
    );
  }
}

SidebarContainer.propTypes = {
  className: PropTypes.string,
  currentUser: PropTypes.object.isRequired
};

export const mapStateToProps = state => ({
  currentUser: currentUserSelector(state)
});

SidebarContainer.defaultProps = {
  className: 'sidebar'
};

export default connect(mapStateToProps, null)(SidebarContainer);
