import { types, createReducer } from 'shared/lib/redux-utils';

import {
  LOAD_ALL_ACCOUNTS,
  CHANGE_STATE,
  LOAD_ALL_LICENSES,
  LICENSE_FULL_REPORT,
  LOAD_ALL_PROTOTYPES,
  LOAD_ALL_DATATYPES,
  LOAD_ALL_FORMS,
  LOAD_SESSIONS,
  PROVIDE_DB_LAST_BACKUP,
  PROVIDE_DB_STATUS,
  LOAD_STORAGE_POINTS,
  LOAD_OBJECT_DETAILS,
  PROVIDE_SERVERS_USAGE,
  SET_STORAGE_NS,
  SET_SELECTED_OBJECT,
  LOAD_STORAGE_DETAILS,
  LOAD_PROTOCOLS
} from '../actions';
import { mapAccountsTree, mapAccountsNewStructure, mapProtocols } from '../mappers';

export const initialState = {
  accounts: [],
  rawAccounts: [],
  flags: {
    accounts: { loading: false },
    protocols: { loading: false },
    sessions: { loading: false },
    storage: { loadingPoints: false, loadingDetails: false },
    objects: { loading: false },
    license: { loading: false },
    reports: { loading: false },
    prototypes: { loading: false },
    datatypes: { loading: false },
    forms: { loading: false },
    serversUsage: { loading: true }
  },
  sessions: [],
  storage: {
    points: [],
    selected: null,
    details: {}
  },
  objects: {
    selectedOwner: null,
    selectedObject: null
  },
  licenses: [],
  reports: {},
  prototypes: [],
  protocols: [],
  datatypes: [],
  forms: [],
  state: {
    path: '',
    level: 'all-dbms'
  },
  serversUsage: {},
  dbLastBackup: [],
  dbStatus: []
};

export const producers = {
  [types.loading(LOAD_ALL_ACCOUNTS)]: draft => {
    draft.flags.accounts.loading = true;
  },
  [types.success(LOAD_ALL_ACCOUNTS)]: (draft, { payload }) => {
    draft.accounts = mapAccountsTree(payload.data.accounts);
    draft.structured = mapAccountsNewStructure(payload.data.accounts, payload.protocols);
    draft.rawAccounts = payload.data.accounts;

    draft.state.path = '';
    draft.state.level = 'all-dbms';

    draft.flags.accounts.loading = false;
  },
  [types.loading(LOAD_PROTOCOLS)]: draft => {
    draft.flags.protocols.loading = true;
  },
  [types.success(LOAD_PROTOCOLS)]: (draft, { payload }) => {
    draft.protocols = mapProtocols(payload.data.protocols);
    draft.flags.protocols.loading = false;
  },
  [types.fail(LOAD_PROTOCOLS)]: draft => {
    draft.flags.protocols.loading = false;
  },
  [CHANGE_STATE]: (draft, { payload }, base) => {
    draft.state = { ...base.state, ...payload };
  },
  [types.loading(LOAD_ALL_LICENSES)]: draft => {
    draft.flags.license.loading = true;
  },
  [types.success(LOAD_ALL_LICENSES)]: (draft, { payload }) => {
    draft.licenses = payload.data.invocations;
    draft.flags.license.loading = false;
  },
  [types.loading(LICENSE_FULL_REPORT)]: draft => {
    draft.flags.reports.loading = true;
  },
  [types.success(LICENSE_FULL_REPORT)]: (draft, { payload }, base) => {
    draft.reports = { ...base.reports, ...payload };
    draft.flags.reports.loading = false;
  },
  [types.loading(LOAD_ALL_PROTOTYPES)]: draft => {
    draft.flags.prototypes.loading = true;
  },
  [types.success(LOAD_ALL_PROTOTYPES)]: (draft, { payload }) => {
    draft.prototypes = payload.data.prototypes;
    draft.flags.prototypes.loading = false;
  },
  [types.loading(LOAD_ALL_DATATYPES)]: draft => {
    draft.flags.datatypes.loading = true;
  },
  [types.success(LOAD_ALL_DATATYPES)]: (draft, { payload }) => {
    draft.datatypes = payload.data.data_types;
    draft.flags.datatypes.loading = false;
  },
  [types.loading(LOAD_ALL_FORMS)]: draft => {
    draft.flags.forms.loading = true;
  },
  [types.success(LOAD_ALL_FORMS)]: (draft, { payload }) => {
    draft.forms = payload.data.forms;
    draft.flags.forms.loading = false;
  },
  [types.loading(PROVIDE_SERVERS_USAGE)]: draft => {
    draft.flags.serversUsage.loading = true;
  },
  [types.success(PROVIDE_SERVERS_USAGE)]: (draft, { payload }) => {
    draft.flags.serversUsage.loading = false;
    draft.serversUsage = payload;
  },
  [types.loading(LOAD_STORAGE_POINTS)]: draft => {
    draft.flags.storage.loadingPoints = true;
  },
  [types.success(LOAD_STORAGE_POINTS)]: (draft, { payload }) => {
    draft.flags.storage.loadingPoints = false;
    draft.storage.points = payload;
  },
  [types.loading(LOAD_STORAGE_DETAILS)]: draft => {
    draft.flags.storage.loadingDetails = true;
  },
  [types.success(LOAD_STORAGE_DETAILS)]: (draft, { payload }) => {
    draft.flags.storage.loadingDetails = false;
    draft.storage.details = payload;
  },
  [types.success(SET_STORAGE_NS)]: (draft, { payload }) => {
    draft.storage.selected = payload;
  },
  [types.loading(LOAD_OBJECT_DETAILS)]: draft => {
    draft.flags.objects.loading = true;
  },
  [types.success(LOAD_OBJECT_DETAILS)]: (draft, { payload }) => {
    draft.flags.objects.loading = false;
    payload.forEach(({ name, res }) => {
      draft.objects[name] = res;
    });
  },
  [types.success(SET_SELECTED_OBJECT)]: (draft, { payload }) => {
    draft.objects.selectedObject = payload.object;
    draft.objects.selectedOwner = payload.owner;
  },
  [types.success(LOAD_SESSIONS)]: draft => {
    draft.flags.sessions.loading = true;
  },
  [types.success(LOAD_SESSIONS)]: (draft, { payload }) => {
    draft.flags.sessions.loading = false;
    // sort flags should be sent to backend and not stored on state.
    draft.flags.sessions.sort = payload.sort;
    draft.sessions = payload.data;
  },
  [types.success(PROVIDE_DB_LAST_BACKUP)]: (draft, { payload }) => {
    draft.dbLastBackup = payload;
  },
  [types.success(PROVIDE_DB_STATUS)]: (draft, { payload }) => {
    draft.dbStatus = payload;
  }
};

export default createReducer(producers, initialState);
